<template>
  <div>
    <img alt="Vue logo" src="../assets/logo.png" height="80">
    <h2>Oracle Access Control</h2>
    <div style="width: 500px; text-align: left; padding: 0 50px; margin: auto;">
      <button v-if="connectedAccount === null" @click="connectWallet">Connect Wallet</button>
      <div v-else>
        <h5>{{ connectedAccount }}</h5>
        <h4 v-if="connectedAccount === OWNER_ADDRESS" style="color: #009dff">%%% Welcome Contract Owner %%%</h4>
        <h4 v-else style="color: red;">xxx You are not contact owner xxx</h4>
      </div>

      <div>
        <h3>Verify Whitelist</h3>
        <input type="text" v-model="verifyAddress" style="width: 350px"/>&nbsp;&nbsp;
        <button @click="verifyAddressOnClicked">Verify</button>
      </div>

      <div>
        <h3>Add Whitelist</h3>
        <input type="text" v-model="addAddress" style="width: 350px"/>&nbsp;&nbsp;
        <button @click="addAddressOnClicked" :disabled="this.addAddress.trim() === ''">Add Whitelist</button>
      </div>

      <div>
        <h3>Remove Whitelist</h3>
        <input type="text" v-model="removeAddress" style="width: 350px"/>&nbsp;&nbsp;
        <button @click="removeAddressOnClicked" :disabled="this.removeAddress.trim() === ''">Remove Whitelist</button>
      </div>
    </div>
  </div>
</template>

<script>
import Web3 from 'web3'
import StakePoolsAbi from '@/abi/StakePools.json'

const contractAddress = process.env.VUE_APP_CONTRACT_STAKE_POOLS
const ORACLE_LOTTERY_THAI_POOL_NAME = 'ORACLE_LOTTERY_THAI'

const OWNER_ADDRESS = '0xdf41c2f7be26dc79153114d8bf47ff515e3779fd'

export default {
  name: 'OracleAcl',
  components: {
  },
  data () {
    return {
      OWNER_ADDRESS: OWNER_ADDRESS,
      eth: null,
      connectedAccount: null,
      contract: {},
      verifyAddress: '',
      addAddress: '',
      removeAddress: ''
    }
  },
  async created () {
    if (typeof window.ethereum === 'undefined') {
      return
    }

    this.eth = window.ethereum
    await this._requestAccount()
  },
  methods: {
    async connectWallet () {
      await this._requestAccount()
    },
    async verifyAddressOnClicked () {
      if (this.verifyAddress.trim() === '') {
        return
      }

      const result = await this.contract.methods
        .verifyWhiteList(ORACLE_LOTTERY_THAI_POOL_NAME, this.verifyAddress)
        .call()

      const msg = `${this.verifyAddress} is whitelist => ${result}`
      alert(msg)
    },
    async addAddressOnClicked () {
      if (this.addAddress.trim() === '') {
        return
      }

      const result = await this.contract.methods
        .addWhiteList(ORACLE_LOTTERY_THAI_POOL_NAME, this.addAddress)
        .send({ from: this.connectedAccount })

      alert(`Add ${this.addAddress} to Whitelist Done.`)

      console.log(this.connectedAccount)
      console.log(result)
    },
    async removeAddressOnClicked () {
      if (this.removeAddress.trim() === '') {
        return
      }

      const result = await this.contract.methods
        .removeWhiteList(ORACLE_LOTTERY_THAI_POOL_NAME, this.removeAddress)
        .send({ from: this.connectedAccount })

      alert(`Remove ${this.addAddress} to Whitelist Done.`)

      console.log(this.connectedAccount)
      console.log(result)
    },
    async _requestAccount () {
      try {
        const accounts = await this.eth.request({ method: 'eth_requestAccounts' })
        if (accounts.length > 0) {
          this.connectedAccount = accounts[0]

          const web3 = new Web3(this.eth)
          this.contract = new web3.eth.Contract(StakePoolsAbi, contractAddress)
        }
      } catch ($ex) {}
    }
  }
}
</script>
