import Vue from 'vue'
import Router from 'vue-router'
import LotteryThai from '@/views/LotteryThai'
import SubscribePool from '@/views/SubscribePool'
import OracleAcl from '@/views/OracleAcl'
import LockWallet from '@/views/LockWallet/LockWallet'
import SubscribeRoute from '@/views/Subscribe/router'

Vue.use(Router)

export default new Router({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'LotteryThai',
      component: LotteryThai
    },
    {
      path: '/subscribe-pool',
      name: 'SubscribePool',
      component: SubscribePool
    },
    {
      path: '/oracle-acl',
      name: 'OracleAcl',
      component: OracleAcl
    },
    {
      path: '/wallet',
      name: 'LockWallet',
      component: LockWallet
    },
    {
      path: '/subscribe',
      component: () => import(/* webpackChunkName: "user" */ '@/views/Subscribe/Index.vue'),
      children: SubscribeRoute
    }
  ]
})
