<template>
  <div>
    <div style="float: right">
      <el-button size="small" type="default" style="margin: 10px" @click="gotoDemoPage">
        Demo
      </el-button>
    </div>
    <div style="text-align: center">
      <img alt="Vue logo" src="../assets/logo.png" height="80">
      <h2>Subscribe Pool</h2>
      <div style="width: 500px; text-align: left; padding: 0 50px; margin: auto;">
        <button v-if="connectedAccount === null" @click="connectWallet">Connect Wallet</button>
        <div v-else>
          <h5>{{ connectedAccount }}</h5>
        </div>

        <h2>Oracle: Lottery Thai</h2>
        <fieldset>
          <legend>Check Expire</legend>
          <input type="text" v-model="checkExpireAddress" style="width: 350px"/>&nbsp;&nbsp;
          <button @click="checkExpireAddressOnClicked">check</button>
          <br>
          <label>{{ expireTime }}</label>
        </fieldset>

        <fieldset style="margin-top: 30px;">
          <legend>Purchase</legend>
          <input type="text" v-model="purchaseAddress" style="width: 350px"/>&nbsp;&nbsp;
          <button @click="purchaseAddressOnClicked">Purchase</button>
        </fieldset>

      </div>
    </div>
  </div>
</template>

<script>
import Web3 from 'web3'
import SubscribePoolsAbi from '@/abi/SubscribePools.json'

const contractAddress = process.env.VUE_APP_CONTRACT_SUBSCRIBE_POOLS
const ORACLE_LOTTERY_THAI_POOL_NAME = 'ORACLE_LOTTERY_THAI'

export default {
  name: 'OracleAcl',
  components: {
  },
  data () {
    return {
      eth: null,
      connectedAccount: null,
      contract: {},
      purchaseAddress: '',
      checkExpireAddress: '',
      expireTime: 'N/A'
    }
  },
  async created () {
    if (typeof window.ethereum === 'undefined') {
      return
    }

    this.eth = window.ethereum
    await this._requestAccount()
  },
  methods: {
    async connectWallet () {
      await this._requestAccount()
    },
    async checkExpireAddressOnClicked () {
      if (this.checkExpireAddress.trim() === '') {
        return
      }

      const result = await this.contract.methods
        .expireTimeByPoolContract(ORACLE_LOTTERY_THAI_POOL_NAME, this.checkExpireAddress)
        .call()

      const expire = parseInt(result)
      if (expire === 0) {
        this.expireTime = 'N/A'
      } else {
        this.expireTime = new Date(expire * 1000)
      }
    },
    async purchaseAddressOnClicked () {
      if (this.purchaseAddress.trim() === '') {
        return
      }

      await this.contract.methods
        .purchase(ORACLE_LOTTERY_THAI_POOL_NAME, this.purchaseAddress)
        .send({ from: this.connectedAccount, value: Web3.utils.toWei('0.01') })

      alert(`Purchase success.`)
    },
    async _requestAccount () {
      try {
        const accounts = await this.eth.request({ method: 'eth_requestAccounts' })
        if (accounts.length > 0) {
          this.connectedAccount = accounts[0]

          const web3 = new Web3(this.eth)
          this.contract = new web3.eth.Contract(SubscribePoolsAbi, contractAddress)
        }
      } catch ($ex) {}
    },
    gotoDemoPage: function () {
      this.$router.push({
        name: 'LotteryThai'
      })
    }
  }
}
</script>
